.header {
  position: fixed;
  z-index: 10;
  bottom: 0;
  left: 0;
  width: 100%;
  height: $height;
  display: flex;
  background-color: $main-dark;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 5%;
  position: relative;
  z-index: 5;
  background-color: inherit;
  gap: 5%;
}

.menu__wrapper {
  display: flex;
  align-items: center;
  gap: 20%;
  width: 50%;
}

.right-wrapper {
  justify-content: flex-end;
}

.menu__lang-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  width: 60%;
  height: 70px;
  margin: 0;
  padding: 10px 5% 5px 5%;
  font-family: 'Bebas Neue Cyrillic';
  @include adaptiv-font (20, 14);
  gap: 5px;
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: rgba(30, 30, 30, 0.4);
  transform-origin: 100% 50%;
  transition: transform $transition;
  z-index: 12;
  backdrop-filter: blur(5px);
  transform: scaleX(0);
}

.menu__lang-item  {
  opacity: 0;
  margin-top: 3px;
}

.menu__lang-item a {
  width: 40px;
  height: 40px;
  color: $main-light;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s $cubic;
  font-size: 40px;
}

.navigation__item {
  margin-bottom: 20px;
}

.navigation__item {
  transform: translateX(100px);
  opacity: 0;
  transition: all $transition;
}

.navigation__item a {
  text-decoration: none;
  color: $main-dark;
  display: block;
  width: fit-content;
  position: relative;
  transition: all $transition;
  text-align: right;
}

.navigation__item:active {
  color: #575757;
}

.navigation__item a::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: $main-dark;
  left: 0;
  bottom: 0;
  transform: scaleX(0);
  transform-origin: left center;
  transition: transform $transition;
}

.menu__blog-link {
  display: block;
  width: fit-content;
  height: 40px;
  color: white;
  font-family: 'Bebas Neue Cyrillic';
  @include adaptiv-font(60, 25);
  text-decoration: none;
  margin-bottom: 5px;
}

.menu__lang-item button:active {
  color: #E6E6E6;
}

.menu__lang-item button::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: $main-light;
  transform: scaleX(0);
  transition: transform $transition;
  transform-origin: left center;
}

.menu__lang-item button:active:before {
  background-color: #E6E6E6;
}

.navigation__list {
  font-family: 'Bebas Neue Cyrillic';
  @include adaptiv-font(70, 30);
  display: flex;
  list-style: none;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  width: fit-content;
  align-self: flex-end;
  align-items: flex-end;
}

.menu__logo {
  margin-bottom: 9px;
}

.menu__logo img {
  width: 65px;
  height: 45px;
}

.menu__btn {
  width: 40px;
  height: 40px;
  position: relative;
  flex: 0 0 auto;
  transition: transform $transition;
  transform: scale(1);
}

.menu__btn:active {
  transform: scale(0.9);
}

.btn__line {
  position: absolute;
  display: block;
  height: 5px;
  background-color: $main-light;
  width: 100%;
  transition: transform $transition;
}

.btn__line--1 {
  top: 15%;
  left: 0;
}

.btn__line--2 {
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.btn__line--3 {
  bottom: 15%;
  left: 0;
}

.navigation {
  width: 100%;
  height: 100vh;
  height: var(--windowInnerHeight, 100vh);
  z-index: 3;
  padding: calc(5% + $height) 5% 5% 5%;
  background-color: $main-light;
  position: absolute;
  top: 0;
  left: 50%;
  transform-origin: 50% bottom;
  transform: translate(-50%, -100%) scaleY(0);
  transition: scaleY $transition;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 5%;
}

.communication {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: flex-end;
}

.communication a {
  @include adaptiv-font (20, 14);
  color: $main-dark;
  margin-bottom: 15px;
  text-decoration: none;
  font-family: 'Eastman';
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation__list svg {
  width: 20px;
  height: 20px;
  transition: fill $transition;
}

.communication__item {
  margin-right: 20px;
  opacity: 0;
  transition: opacity $transition;
}

.communication__phone {
  opacity: 0;
  transition: opacity $transition;
}

.communication__mail {
  opacity: 0;
  transition: opacity $transition;
}

.navigation__item a:active {
  color: #575757;
}

.navigation__item a:active:before {
  background-color: #575757;
}

.communication a:active {
  color: #575757;
}

.menu__lang-item--active {
  & button {
    background-color: #ff7622;
    // background-image: $gradient;
    // -webkit-background-clip: text;
    // -webkit-text-fill-color: transparent;
  }
}

.menu__lang-btn {
  position: relative;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;

  & svg {
    width: 30px;
    height: 30px;
  }

  & span {
    position: absolute;
    top: 0;
    right: 0;
    font-family: 'Eastman';
    color: #ff602c;
    font-weight: 700;
    font-size: 14px;
  }
}

.blog-link {
  display: block;
  font-family: 'Bebas Neue Cyrillic';
  font-size: 40px;
  font-weight: 400;
  margin-top: 8px;
  line-height: 1;
  color: $main-light;
  text-decoration: none;
  transition: all 0.2s $cubic;
}

.blog-link:active {
  transform: scale(0.9);
}

@media (min-width: 768px) {
  .header {
    top: 0;
  }

  .navigation {
    padding: 3% 5% calc(3% + $height) 5%;
  }

  .menu__lang-list {
    justify-content: flex-start;
    flex-basis: 100%;
    margin-right: 0;
  }
}

@media (min-width: 1440px) {
  .menu {
    padding: 0;
  }

  .navigation__list svg {
    width: 40px;
    height: 40px;
  }

  .container {
    width: 1440px;
    margin: 0 auto;
    padding: 0 30px;
  }
}

@media (any-hover: hover) {
  .communication a:hover {
    text-decoration: underline;
  }

  .navigation__item a:hover {
    background: $gradient;
    background-clip: text;
    color: transparent;
  }

  .menu__lang-item button:hover {
    background: $gradient;
    background-clip: text;
    color: transparent;
  }

  .blog-link:hover {
    background: $gradient;
    background-clip: text;
    color: transparent;
  }

  .menu__btn:hover .btn__line--1 {
    transform: translateY(1px);
  }

  .menu__btn:hover .btn__line--3 {
    transform: translateY(-1px);
  }
}

